import React from "react";
 
const Header = (props) => {
    return (
        <div className="header">
            <a href="/">
                <img src="images/logo-light.png" alt="logo protelkom" />
            </a>
            <h1>{ props.title }</h1>
            <div></div>
        </div>
    );
};
 
export default Header;