import React from "react";
import Header from "../components/Header";
 
const Skup = () => {
    return (
        <main>
            <Header title="Skup" />
            <div className="section menu-space">
                <h2>Skup urzdze</h2>
                <p>Masz nieuywane lub uszkodzone urzdzenie, ktre zalega w szufladzie? Nie wyrzucaj go! Oferujemy szybki i prosty skup telefonw, komputerw, tabletw oraz innych urzdze elektronicznych. Dziki nam, zyskasz gotwk za sprzt, ktrego ju nie potrzebujesz. Sprawd, ile moesz zyska  wystarczy, e zadzwonisz lub dostarczysz sprzt do punktu, a my szybko przedstawimy Ci ofert.</p>
            </div>
            <div className="red-list-container section">
                <h2>Jak to dziaa<span>?</span></h2>
                <div className="item-list"><div><h2 className="number-list">1</h2><div><p className="text-list">Skontaktuj si z nami telefonicznie lub przez formularz na stronie.</p></div></div></div>
                <div className="item-list"><div><h2 className="number-list">2</h2><div><p className="text-list">Przeprowadzamy dokadn diagnoz problemu.</p></div></div></div>
                <div className="item-list"><div><h2 className="number-list">3</h2><div><p className="text-list">Nasz zesp przystpuje do naprawy sprztu.</p></div></div></div>
            </div>
            <div className="section wave-container">
                <h2 className="question-h2">Co odkupujemy<span>?</span></h2>
                <p>Smartfony, telefony, tablety, laptopy, komputery stacjonarne, monitory, konsole</p>
            </div>
            <div className="section">
                <h2>Sprzeda urzdze</h2>
                <p>Poszukujesz nowego telefonu, komputera lub innego sprztu w korzystnej cenie? U nas znajdziesz szerok ofert sprawdzonych urzdze, ktre przeszy gruntowny przegld techniczny. Oferujemy urzdzenia zarwno nowe jak i uywane w doskonaym stanie technicznym, z gwarancj oraz wsparciem posprzedaowym.</p>
            </div>
            <div className="section">
                <h2 className="question-h2 bottom-space">Co znajdziesz w naszej ofercie<span>?</span></h2>
                <p><b>Telefony</b>  od najnowszych smartfonw po bardziej budetowe modele. Kady telefon jest przetestowany, a jego stan techniczny jest szczegowo opisany.
                </p>
                <p><b>Komputery i laptopy</b>  idealne rozwizania dla studentw, profesjonalistw i graczy. Nasze komputery to niezawodne maszyny, ktre sprostaj kademu zadaniu.</p>
                <p><b>Tablety i akcesoria</b>  sprawdzone tablety oraz szeroki wybr akcesoriw, ktre uczyni korzystanie z urzdze jeszcze wygodniejszym.</p>
            </div>
            <div className="section">
                <div className="row-section">
                    <h2 className="question-h2 container-50 bottom-space">
                        Dlaczego warto<br />kupowa u nas<span>?</span>
                    </h2>
                    <div className="scroll-item-container container-50">
                        <div className="scroll-item">
                            <h2 className="title-scroll-item">Gwarancja na kady produkt</h2>
                            <p>Kade urzdzenie objte jest gwarancj, co daje Ci pewno bezproblemowego uytkowania.</p>
                        </div>
                        <div className="scroll-item">
                            <h2 className="title-scroll-item">Wsparcie techniczne</h2>
                            <p>Nasz zesp specjalistw jest gotowy, by pomc Ci w kadym momencie.</p>
                        </div>
                        <div className="scroll-item">
                            <h2 className="title-scroll-item">Atrakcyjne ceny</h2>
                            <p>Oferujemy konkurencyjne ceny na wszystkie nasze produkty.</p>
                        </div>
                    </div>
                </div>
            </div>
        </main>
    );
};
 
export default Skup;