import React from "react";
import Header from "../components/Header";
 
const Regulamin = () => {
    return (
        <main>
            <Header title="Regulamin" />
            <div className="regulamin-section section">
                <h2>I. Postanowienia oglne</h2>

                <p><strong>1. Definicje:</strong></p>
                <ul>
                    <li><strong>Serwis</strong> - Usugi naprawy i konserwacji sprztu komputerowego wiadczone przez [Nazwa Firmy].</li>
                    <li><strong>Klient</strong> - Osoba fizyczna lub prawna korzystajca z usug serwisu komputerowego.</li>
                    <li><strong>Regulamin</strong> - Niniejszy regulamin okrelajcy zasady korzystania z usug serwisu komputerowego.</li>
                </ul>

                <p><strong>2. Postanowienia wstpne:</strong></p>
                <ul>
                    <li>Niniejszy regulamin obowizuje od momentu opublikowania go na stronie internetowej serwisu komputerowego [Adres Strony].</li>
                    <li>Korzystanie z usug serwisu oznacza akceptacj niniejszego regulaminu.</li>
                </ul>

                <h2>II. Zakres usug</h2>

                <p><strong>1. Rodzaje usug:</strong></p>
                <ul>
                    <li>Serwis oferuje napraw i konserwacj komputerw, laptopw, oraz innych urzdze elektronicznych.</li>
                    <li>Usugi obejmuj diagnostyk, napraw sprztu, instalacj oprogramowania, oraz inne prace zgodnie z ofert na stronie internetowej serwisu.</li>
                </ul>

                <p><strong>2. Proces realizacji usug:</strong></p>
                <ul>
                    <li>Klient zgasza urzdzenie do naprawy poprzez formularz kontaktowy, telefonicznie lub osobicie w siedzibie serwisu.</li>
                    <li>Po wstpnej diagnostyce Klient zostaje poinformowany o kosztach i czasie realizacji usugi.</li>
                    <li>Wykonanie usugi nastpuje po akceptacji kosztw przez Klienta.</li>
                </ul>

                <h2>III. Ceny i patnoci</h2>

                <p><strong>1. Cennik usug:</strong></p>
                <ul>
                    <li>Cennik usug jest dostpny na stronie internetowej serwisu i moe ulec zmianie.</li>
                    <li>W przypadku koniecznoci wykonania dodatkowych prac, Klient jest informowany o ich kosztach przed rozpoczciem naprawy.</li>
                </ul>

                <p><strong>2. Sposb patnoci:</strong></p>
                <ul>
                    <li>Patno za usug jest dokonywana gotwk, przelewem bankowym lub kart patnicz po zakoczeniu naprawy, chyba e strony ustaliy inaczej.</li>
                </ul>

                <h2>IV. Gwarancja i reklamacje</h2>

                <p><strong>1. Gwarancja:</strong></p>
                <ul>
                    <li>Serwis udziela gwarancji na wykonane usugi oraz na wymienione czci, zgodnie z obowizujcymi przepisami prawa.</li>
                    <li>Okres gwarancji wynosi [np. 3 miesice], chyba e ustalono inaczej.</li>
                </ul>

                <p><strong>2. Reklamacje:</strong></p>
                <ul>
                    <li>Klient ma prawo do zoenia reklamacji w przypadku niewaciwego wykonania usugi lub wadliwych czci.</li>
                    <li>Reklamacje naley zgasza w formie pisemnej na adres e-mail lub listownie do siedziby serwisu.</li>
                    <li>Serwis zobowizuje si do rozpatrzenia reklamacji w terminie [np. 14 dni roboczych] od daty jej otrzymania.</li>
                </ul>

                <h2>V. Odpowiedzialno</h2>

                <p><strong>1. Odpowiedzialno serwisu:</strong></p>
                <ul>
                    <li>Serwis nie ponosi odpowiedzialnoci za dane znajdujce si na urzdzeniach przekazanych do naprawy. Klient powinien zabezpieczy dane przed przekazaniem urzdzenia do serwisu.</li>
                    <li>Serwis nie ponosi odpowiedzialnoci za ewentualne szkody wynike z opnie w realizacji usug, jeli s one spowodowane okolicznociami niezalenymi od serwisu.</li>
                </ul>

                <p><strong>2. Odpowiedzialno Klienta:</strong></p>
                <ul>
                    <li>Klient zobowizany jest do podania penych i prawdziwych informacji dotyczcych stanu technicznego urzdzenia.</li>
                    <li>Klient ponosi odpowiedzialno za dostarczenie do serwisu urzdzenia, ktre nie zawiera legalnego oprogramowania lub jest wykorzystywane niezgodnie z prawem.</li>
                </ul>

                <h2>VI. Polityka prywatnoci</h2>

                <p><strong>1. Dane osobowe:</strong></p>
                <ul>
                    <li>Serwis zbiera i przetwarza dane osobowe Klientw zgodnie z obowizujcymi przepisami prawa oraz Polityk Prywatnoci dostpn na stronie internetowej serwisu.</li>
                </ul>

                <h2>VII. Postanowienia kocowe</h2>

                <p><strong>1. Zmiany regulaminu:</strong></p>
                <ul>
                    <li>Serwis zastrzega sobie prawo do zmiany niniejszego regulaminu. Zmiany obowizuj od momentu ich opublikowania na stronie internetowej serwisu.</li>
                </ul>

                <p><strong>2. Prawo waciwe:</strong></p>
                <ul>
                    <li>W sprawach nieuregulowanych niniejszym regulaminem zastosowanie maj przepisy prawa polskiego.</li>
                </ul>

                <p><strong>3. Rozstrzyganie sporw:</strong></p>
                <ul>
                    <li>Wszelkie spory wynikajce z realizacji usug bd rozstrzygane przez sd waciwy dla siedziby serwisu.</li>
                </ul>
            </div>
        </main>
    );
};
 
export default Regulamin;