import React from "react";

import { useEffect, useRef } from 'react';
import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

// import Swiper core and required modules
import { Autoplay, Pagination, A11y } from 'swiper/modules';
import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import Testimonial from "../components/Testimonial";

gsap.registerPlugin(ScrollTrigger);

const Home = () => {
    
    const ref = useRef();
    useEffect(() => {
        const el = ref.current;
        
        gsap.fromTo(".logo-main-page", {
                y: "22vh",
                x: "18vw",
                scale: 3,
            }, {
                y: "0",
                x: "0",
                scale: 1,
                yPercent: 0,
                scrollTrigger: {
                
            scrub: true,
            trigger: ".dark-section",
            start: "top bottom",
            endTrigger: ".dark-section",
            end: "top center",
            ease: "easeInOut"
            }}
        )
    }, [])
    return (
        <main>
            <div className="header-main-page">
                <div className="header-bg-main-page"></div>
                <img className="logo-main-page" src="images/logo.svg" alt="logo-protelkom" />
            </div>
            <div className="header-main-page-section">
                <div className="header-main-page-subtitle">
                    <h2>Profesjonalny Serwis Telefonw</h2>
                    <p><b></b>Naprawa telefonw komrkowych w Bdzin</p>
                </div>
                <img src="images/bitmapa.png" className="header-logo" alt="logo protelkom"/>
            </div>
            <div className="dark-section section">
                <div className="with-u-section row-section">
                    <div className="with-u-text">
                        <h3>Jestemy z wami od {(new Date().getFullYear())-2005} lat!</h3>
                        <p>Dziki wieloletniemu dowiadczeniu w brany telekomunikacyjnej, doskonale wiemy, jak wane jest prawidowe zdiagnozowanie usterki oraz dobr odpowiednich czci, aby naprawa speniaa standardy najwyszej jakoci. {(new Date().getFullYear())-2005} lat dowiadczenia w naprawach telefonw pozwala nam diagnozowa usterki z niemal 100% skutecznoci.</p>
                    </div>
                    <img src="images/stanowiska.jpg" alt="stanowiska w protelkom" />
                </div>
            </div>
            <div className="section">
                <div className="scroll-section row-section">
                    <h2 className="question-h2">
                        Dlaczego warto <br />wybra PROtelkom<span>?</span>
                    </h2>
                    <div className="scroll-item-container">
                        <div className="scroll-item">
                            <h2 className="title-scroll-item">Dowiadczenie i profesjonalizm</h2>
                            <p>Nasi technicy maj wieloletnie dowiadczenie i szerok wiedz techniczn.</p>
                        </div>
                        <div className="scroll-item">
                            <h2 className="title-scroll-item">Indywidualne podejcie</h2>
                            <p>Kady klient jest dla nas wany, dlatego oferujemy spersonalizowane rozwizania dopasowane do Twoich potrzeb.</p>
                        </div>
                        <div className="scroll-item">
                            <h2 className="title-scroll-item">Szybko i efektywno</h2>
                            <p>Staramy si realizowa naprawy w moliwie najkrtszym czasie.</p>
                        </div>
                        <div className="scroll-item">
                            <h2 className="title-scroll-item">Gwarancja jakoci</h2>
                            <p>Uywamy oryginalnych lub wysokiej jakoci czci zamiennych i oferujemy gwarancj na wykonane usugi.</p>
                        </div>
                    </div>
                </div>
            </div>
            <div className="red-section section">
                <h2 class="subtitle">Opinie naszych klientw</h2>
                <Swiper
                className="testimonials-swiper"
                // install Swiper modules
                modules={[Autoplay, Pagination, A11y]}
                spaceBetween={50}
                slidesPerView={1}
                pagination={{ clickable: true }}        
                loop={true}    
                autoplay={{delay:7000}}
                speed={3000}   
                
                breakpoints={{
                    // when window width is >= 768px
                    800: {
                    slidesPerView: 2,
                    },
                    1200: {
                    slidesPerView: 3,
                    },
                }}         
                onSwiper={(swiper) => console.log(swiper)}
                >
                    <SwiperSlide><Testimonial customerName="Magdalena O." customerFirstLetter="M" customerOpinion="Panowie bardzo pomocni i uprzejmi.
                                Cho mieli duo zlece, zrozumieli moj sytuacj i przyjli zlecenie.
                                Fachowo, uprzejmo i wyrozumiao to rzadko w usugach na rynku.
                                Polecono mi ten punkt i ja te go bd dalej poleca." /></SwiperSlide>
                    <SwiperSlide><Testimonial customerName="Adrian S." customerFirstLetter="A" customerOpinion="Szkoda, e tu tylko mona 5 gwiazdek da, bo serwis zasuguje na duo wicej!
                            Polecam!" /></SwiperSlide>
                    <SwiperSlide><Testimonial customerName="Pawe B." customerFirstLetter="P" customerOpinion="Kolejna wizyta w tym miejscu i nadal wysoki poziom. Sprzt, akcesoria tasze od innych miejsc. Do kadego klienta podejcie rewelacyjne, a jeeli czego brak szybkie sprowadzenie. Profesjonalna pomoc w kadym przypadku bez nacigania. Sprzt i akcesoria na kad kiesze. Tak trzyma  godne polecenia" /></SwiperSlide>
                    <SwiperSlide><Testimonial customerName="Stanisaw P." customerFirstLetter="S" customerOpinion="Szybko, na miejscu i profesjonalnie. Mia obsuga, doradzi i podpowie, kolejny raz odwiedzam i za kadym razem jestem zadowolony. Wymiana baterii w telefonie, folia ochronna, etui, adowarka. Dwa razy taniej ni w media... Do tego dodatkowa folia na zapas w cenie." /></SwiperSlide>
                    <SwiperSlide><Testimonial customerName="Eliza J." customerFirstLetter="E" customerOpinion="Peen profesjonalizm! Panowie znaj si na rzeczy! Ceny naprawy oraz akcesoria do telefonw na maxa konkurencyjne, porwnujc inne serwisy.
                            Polecam z czystym sumieniem!" /></SwiperSlide>
                    <SwiperSlide><Testimonial customerName="Micha A." customerFirstLetter="M" customerOpinion="Super obsuga, szybko zdjagnozowana awaria oraz szybko usunita. Fachowa obsuga. Najlepszy punkt serwisowy w jakim byem i z ktrego korzystaem! :)" /></SwiperSlide>                    
                </Swiper>
            </div>
        </main>
    );
};
 
export default Home;